import { ProductModel } from "../src/models/productModel";

const cron = require("node-cron");
const fs = require("fs");

// Start cron jobs
const shoeUpdate = new ProductModel();
cron.schedule("* * * * *", function (err: any) {
    if (err) throw err;
    if (shoeUpdate.updateShoes(2)) {
        fs.appendFile("../update.log", Date.now() + "--- Updated all shoes by $2 successfully", function (err: any) {
            if (err) throw err;
        });
    } else {
        fs.appendFile("../update.log", Date.now() + "--- FAILED TO UPDATE SHOES ***********", function (err: any) {
            if (err) throw err;
        });
    }
});

// Renew update.log. Done every sunday night
cron.schedule("59 59 23 * 0", function () {
    fs.unlink('../update.log', function (err: any) {
        if (err) throw err;
    });
    fs.link('../update.log', function (err: any) {
        if (err) throw err;
        console.log('Update file successfully re-created!');
    })

});
